import { Routes, RouterModule } from '@angular/router';
import { ModuleWithProviders } from '@angular/core';
import { PageGatherMaintainComponent } from './page-gather-maintain/page-gather-maintain.component';
import { PageDefinitonComponent } from './page-gather-maintain/page-gather-definiton/page-gather-definiton.component';

export const routes: Routes = [
    {
        path: '',
        children: [
            {
                path: 'page-gather-maintain',
                component:PageGatherMaintainComponent ,
            },
            {
                path: 'page-gather-definition',
                component:PageDefinitonComponent ,
            },
            
        ]
    }
]
export const PageDataSetRouting: ModuleWithProviders = RouterModule.forChild(routes);